html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

html, body {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  background: #F7F4ED;
  color: #2D5A79; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Zilla Slab", serif; }

a {
  color: #2D5A79;
  text-decoration: none;
  transition: all 0.2s cubic-bezier(0.47, 0, 0.745, 0.715); }
  a:hover {
    color: #FF7B54; }

p {
  line-height: 1.5;
  padding: 12px 0; }

section {
  padding: 96px 0;
  position: relative; }
  section h2 {
    font-size: 32px;
    margin: 0 0 12px; }
  section h3 {
    font-size: 24px;
    margin: 0 0 16px; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.btn {
  display: inline-block;
  padding: 11px 12px;
  border-radius: 3px; }
  .btn.raised {
    box-shadow: 2px 2px 1px rgba(0, 0, 0, 0.1);
    font-family: "Zilla Slab", serif; }
    .btn.raised.primary {
      background-color: #FF7B54;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-weight: 700;
      color: #F7F4ED; }
      .btn.raised.primary:hover {
        background: #2D5A79;
        color: #F7F4ED; }
  .btn.blank:hover, .btn.secondary:hover, .btn.thirdtiary:hover {
    background: rgba(0, 0, 0, 0.05);
    color: inherit; }
  .btn.w-icon {
    text-transform: uppercase;
    letter-spacing: 2px; }
  .btn.thirdtiary {
    color: #FF7B54; }

.section-actions {
  position: absolute;
  bottom: 96px;
  left: 0;
  right: 0; }

#TopPanel {
  background: #4EB7F0;
  color: #F7F4ED;
  min-height: 100vh;
  display: flex;
  align-items: center; }
  #TopPanel .split-panel {
    height: 100%;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center; }
  #TopPanel h1 {
    font-size: 48px;
    margin: 0;
    padding: 0; }
    #TopPanel h1 span {
      font-weight: 300; }
  #TopPanel h2 {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    font-size: 18px;
    margin: 16px 0; }
  #TopPanel .devices {
    display: flex;
    align-items: center; }
    #TopPanel .devices img {
      display: block; }
    #TopPanel .devices .mobile img {
      margin-right: 40px; }
    #TopPanel .devices .bigger img {
      margin-top: 24px; }
      #TopPanel .devices .bigger img:first-child {
        margin-top: 0; }
  #TopPanel .actions .btn {
    margin-right: 12px; }
  #TopPanel .btn.w-icon {
    color: #F7F4ED; }

#AboutPanel {
  background: url(../img/about-bg.png) 115% 50% no-repeat; }
  #AboutPanel .work-logos {
    margin-bottom: 96px; }
    #AboutPanel .work-logos ul {
      list-style: none;
      margin: 24px 0 0;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%; }
      #AboutPanel .work-logos ul li {
        text-align: center; }
        #AboutPanel .work-logos ul li img {
          display: inline-block; }
  #AboutPanel .skills-container {
    max-width: 70%;
    margin-bottom: 96px; }
    #AboutPanel .skills-container .skills {
      display: flex; }
      #AboutPanel .skills-container .skills .skill-set {
        margin-right: 48px; }
        #AboutPanel .skills-container .skills .skill-set ul {
          margin: 0;
          padding: 0;
          list-style: none; }
          #AboutPanel .skills-container .skills .skill-set ul li {
            margin-top: 4px; }

#WorkPanel p {
  max-width: 60%; }

#WorkPanel .work-container ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center; }
  #WorkPanel .work-container ul li:nth-child(even) .case-study {
    margin-top: 48px; }
  #WorkPanel .work-container ul .case-study {
    width: 300px;
    height: 500px;
    border-radius: 5px;
    box-shadow: 0 12px 17px rgba(0, 0, 0, 0.14), 0 5px 22px rgba(0, 0, 0, 0.12), 0 7px 8px rgba(0, 0, 0, 0.2);
    margin-left: 24px;
    marging-right: 24px; }
    #WorkPanel .work-container ul .case-study:hover {
      cursor: pointer; }
    #WorkPanel .work-container ul .case-study.gh {
      background: url(../img/case-study/GamersHive.jpg) 50% 50% no-repeat; }
    #WorkPanel .work-container ul .case-study.mg {
      background: url(../img/case-study/MG.jpg) 50% 50% no-repeat; }
    #WorkPanel .work-container ul .case-study.ac {
      background: url(../img/case-study/AddictionCenter.jpg) 50% 50% no-repeat; }
    #WorkPanel .work-container ul .case-study.cc {
      background: url(../img/case-study/CloudCraft.jpg) 50% 50% no-repeat; }

#ContactPanel {
  background: #4EB7F0; }
  #ContactPanel .container {
    display: flex;
    flex-wrap: wrap; }
  #ContactPanel footer {
    flex: 1 1 60%; }
    #ContactPanel footer p {
      max-width: 60%; }
  #ContactPanel .social ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: right; }
    #ContactPanel .social ul li {
      display: inline-block;
      margin: 0 8px;
      font-size: 24px; }
  #ContactPanel .pride-proud {
    flex: 0 0 100%; }
    #ContactPanel .pride-proud img {
      display: inline-block;
      vertical-align: middle;
      border-radius: 2px;
      margin-left: 8px;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.14), 0 2px 1px rgba(0, 0, 0, 0.12), 0 1px 3px rgba(0, 0, 0, 0.2); }
